import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import Store from '../pages/store';
import About from '../pages/About';
import NotFound from '../pages/NotFound';
import Cart from "../pages/cart";

const Routes = () => {
  return (
    <Router>
        <Switch>
          <Route exact path="/twins-bakery-shop/" component={Store} />
          <Route path="/twins-bakery-shop/about" component={About} />
          <Route path="/twins-bakery-shop/store" component={Store}/>
          <Route path="/twins-bakery-shop/cart" component={Cart} />
          <Route path="*" component={NotFound} />
        </Switch>
    </Router>
  );
}

export default Routes;